AmplifyEventBus.$on('authState', async (state) => {
  console.log('AmplifyEventBus.$on', state)

  if (state === 'signedOut'){
    console.log('signedOut')

    router.push({path: '/auth'})
  } else if (state === 'signedIn') {
    console.log('signedIn')

    await store.dispatch('session/login')
      .catch(e => console.log('login!!!', e))
    
    const rtn = await store.dispatch('checkIn/loadReservation')
      .catch(e => console.log('loadReservation!!!', e))

    if (rtn) {
      console.error(rtn)
      window.alert(rtn.message)

      await store.dispatch('session/logout')
        .catch(e => console.log('login!!!', e))

    } else {
      console.log('store.state.checkIn.reservation', JSON.stringify(store.state.checkIn.reservation))
      router.push({path: '/'})
    }
  }

});

function verifyCheckInDate() {
  const now = new Date()
  const checkInDate = Date.parse(store.state.checkIn.reservation.checkInDate)
  return now.getTime() > checkInDate
}

const router = new Router({
  mode: 'hash',
  // base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: About,
      meta: { requiresAuth: true, noPriorAccess: false }
    },
    {
      path: '/checkIn',
      name: 'checkIn',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: CheckIn,
      meta: { requiresAuth: true, noPriorAccess: true }
    },
    {
      path: '/preCheckIn',
      name: 'preCheckIn',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: PreCheckIn,
      meta: { requiresAuth: true, noPriorAccess: false }
    },
    {
      path: '/auth',
      name: 'Authenticator',
      component: components.Authenticator
    }
  ]
})

router.beforeResolve(async (to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // console.log(JSON.stringify(store.state.checkIn.noPriorAccessAlert))

    let userName = store.state.session.userName
    console.log('router.beforeResolve', userName)
    if (!userName) {

      await store.dispatch('session/logout')
        .catch(e => console.log('login!!!', e))


      // return next({
      //   path: '/auth',
      //   query: {
      //     redirect: to.fullPath
      //   }
      // })
    }


    if (to.matched.some(record => record.meta.noPriorAccess)) {
      if (verifyCheckInDate()) {
        return next()
      } else {
        store.dispatch('checkIn/setNoPriorAccessAlert', true)
        return next({
          path: '/preCheckIn',
          query: {
            redirect: to.fullPath
          }
        })        
      }
    } else {
      return next()  
    }

    
  }
  return next()
})

