import * as AmplifyModules from 'aws-amplify'
import { AmplifyEventBus } from 'aws-amplify-vue'

const state = {
  userName: null,
  userIdentityId: null
}

const mutations = {
  setUserName(state, userName) {
    state.userName = userName
  },
  setUserIdentityId(state, userIdentityId) {
    state.userIdentityId = userIdentityId
  }
}

const actions = {
  async login({ commit }) {
    
    const [authenticatedUser, userInfo] = 
      await Promise.all([AmplifyModules.Auth.currentAuthenticatedUser(), AmplifyModules.Auth.currentUserInfo()])

    if (authenticatedUser && authenticatedUser.signInUserSession) {
      commit('setUserName', authenticatedUser.username)
      commit('setUserIdentityId', userInfo.id)
    }

  },
  async logout({ commit }) {
    commit('setUserName', null)
    commit('setUserIdentityId', null)
  
    await AmplifyModules.Auth.signOut()

    AmplifyEventBus.$emit('authState', 'signedOut')

  }
}

const getters = {
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}